            <select class="form-control" v-model="exam.dependentLesson">
              <option v-for="lesson in lessons" :key="lesson._id" :value="lesson._id">
                <span>{{ lesson.name }}</span>
                <span>/</span>
                <span>{{ lesson.part }}</span>
              </option>
              <option value="">none</option>
            </select>
          </div>
        </div>
        <div v-if="action === 'addExam' || action === 'saved'" class="col-md-2">
          <div class="form-group">
