            </select>
          </div>
        </div>
      </div>
      <hr />
      <!------------------------------------------------------------------>
      <!------------------------------------------------------------------>
      <div class="row justify-content-center" dir="rtl">
        <div class="col-md-3">
          <div class="form-group">
            <label> </label>
            <select class="form-control" v-model="exam.lang">
              <option value="1">arabic</option>
              <option value="2">english</option>
            </select>
          </div>
        </div>
        <div class="nos col-md-3">
          <div class="form-group">
            <label>  </label>
            <input
              class="form-control"
              type="number"
              min="1"
              v-model.number="exam.timer"
            />
          </div>
        </div>
        <div class="col-md-3">
          <div class="form-group">
            <label>  </label>
            <select class="form-control" v-model="exam.randomQuestions">
              <option :value="true"></option>
              <option :value="false"></option>
            </select>
          </div>
        </div>
      </div>
      <hr />
      <!------------------------------------------------------------------>
      <!------------------------------------------------------------------>
      <div v-if="!exam.isTask" class="row justify-content-center" dir="rtl">
        <div class="col-md-4">
          <div class="form-group">
            <label>   </label>
            <datetime
              style="background: #fff; width: fit-content"
              type="datetime"
              placeholder="    "
              use12-hour
              v-model="exam.deadLine"
            ></datetime>
          </div>
        </div>
        <div class="col-md-4">
          <div class="form-group">
            <label> </label>
            <datetime
              style="background: #fff; width: fit-content"
              type="datetime"
              placeholder=" "
              use12-hour
              v-model="exam.publishDate"
            ></datetime>
          </div>
        </div>
        <div class="col-md-4">
          <div class="form-group">
            <label>  </label>
            <datetime
              style="background: #fff; width: fit-content"
              type="datetime"
              placeholder="  "
              use12-hour
              v-model="exam.resultPublishDate"
            ></datetime>
          </div>
        </div>
      </div>
    </div>
    <hr />
    <div class="container explain">
      <h2 class="subtitle">Guidelines</h2>
      <span class="tip"
        >         ...   
                      
                         
            </span
      >

      <span class="tip">
                    ... 
                     
                  
      </span>
    </div>
    <hr />
    <!------------------------------SECTIONS----------------------------->
    <div class="questions">
      <div
        v-for="(section, i) in exam.sections"
        :key="i"
        class="questionsSection"
        style="position: relative"
      >
        <div class="_close-absolute">
          <i @click="removeSection(i)" class="fa fa-times"></i>
        </div>
        <questions-section
          :examName="examName"
          :section="section"
          :index="i"
          :lang="exam.lang"
          :autoCorrect="exam.autoCorrect"
          @concatArraysOfQuestions="concatArraysOfQuestionsHandler"
        />
      </div>
      <!---------
    ---------------------------------------------->
      <div class="row justify-content-center">
        <div class="col-md-4">
          <div class="plus-minus d-flex justify-content-between">
            <button
              @click="increaseOneSection"
              class="btn btn-outline-primary btn-rounded m-1"
            >
              <i class="fa fa-plus"></i>
                
            </button>
          </div>
        </div>
      </div>
    </div>
    <hr />
    <!-------------------------------------------->
    <!-- <div class="container">
      <div>
        <textarea v-model="textToQuestinos" rows="4" cols="12"></textarea>
        <v-btn @click="textToQuestinosHandler" depressed color="primary">
          fill Questions
        </v-btn>
      </div>
    </div> -->
    <!-------------------------------------------->
    <hr />
    <loading v-if="loading" />
    <loading v-if="removing" />
    <div class="row justify-content-center align-items-center">
      <div class="actions col-md-4">
        <button class="btn" @click="addExam"> </button>
      </div>
      <!-- <div v-if="examExist" class="col-md-4">
        <button class="btn btn-success" @click="gotoSolutionModel">
            
        </button>
      </div> -->
      <div class="col-md-4">
        <button @click="copyQuestions" class="_btn btn-1">Copy</button>
      </div>
      <div class="col-md-4">
        <button :disabled="!questionsExist" @click="pasteQuestions" class="_btn btn-2">
          Paste
        </button>
      </div>
      <div class="col-md-4">
        <button @click="download" class="_btn btn-3">Download</button>
      </div>
      <div class="col-md-4">
        <input style="display: none" type="file" ref="upload" @change="getJson" />
        <button @click="$refs.upload.click()" class="_btn btn-4">Upload</button>
      </div>
      <div class="col-md-4">
        <button @click="removeQuestions" class="_btn btn-danger">Empty questions</button>
      </div>
      <div v-if="examId" class="col-md-4">
        <button @click="deleteExam" class="_btn btn-danger">Delete Exam</button>
      </div>
      <div v-if="examId && !exam.isTask" class="col-md-4">
        <button @click="deleteSolutions" class="_btn btn-danger">
          Delete Students solutions
        </button>
      </div>
    </div>
    <!--------------------------------------------------------->
    <app-iframe :src="$store.getters.expVideos.addExam" />
    <!--------------------------------------------------------->
  </div>
</template>

<script>
import axios from "axios";
import QuestionsSection from "./QuestionsSection.vue";
import Loading from "../../../components/loading.vue";
import { eventBus } from "../../../main.js";
import { Datetime } from "vue-datetime";
import { generateRandomString, delay, textToQuestinosHandler } from "../../../functions";
export default {
  created() {
    if (this.$route.params.lessonId) {
      this.exam.isTask = true;
      this.exam.lessonId = this.$route.params.lessonId;
      this.exam.autoCorrect = true;
      axios.get("/fetchVideo/" + this.exam.lessonId).then((res) => {
        this.exam.stage = res.data.video.stage;
        this.exam.unit = res.data.video.unit;
        this.exam.name = res.data.video.name;
      });
    }
    //////////////////////////////////////////////////////
    if (this.$route.params.id) {
      this.action = "editExam";
      this.examId = this.$route.params.id;
      this.fetchAPI = "/fetchUnitExamById/" + this.examId;
    } else {
      this.action = "addExam";
      this.exam.stage = "6";
    }
    //////////////////////////////////////////////////////////

    if (this.action == "editExam") {
      axios.get(this.fetchAPI).then(async (res) => {
        if (res.data.error) return alert("Problem happened");
        if (!res.data.exam) return alert("Exam not found");
        this.exam = res.data.exam;
        console.log(this.exam);
        this.examExist = true;
      });
    }
  },
  components: {
    QuestionsSection,
    Loading,
    Datetime,
  },
  data() {
    return {
      exam: {
        _id: null,
        isTask: false,
        randomQuestions: false,
        name: "",
        number: 1,
        year: new Date().getFullYear(),
        stage: "",
        unit: "",
        lessonId: null,
        autoCorrect: true,
        lang: "1",
        timer: 5,
        fullDegree: 1,
        passingScore: 0,
        dependentLesson: "",
        deadLine: new Date(new Date().getTime() + 604800000)
          .toISOString()
          .substring(0, 10),
        publishDate: new Date().toISOString().substring(0, 10),
        resultPublishDate: new Date().toISOString().substring(0, 10),
        sections: [
          {
            number: 0,
            titleType: "text",
            text: "  ",
            questionsType: 1,
            mimetype: "",
            questions: [
              {
                question: " ",
                contentType: "text",
                number: 0,
                fullDegree: 1,
                mimetype: "",
                choices: ["", "", "", ""],
                modalAnswer: "",
                typicalAnswer: "",
              },
            ],
          },
        ],
      },
      collectedSections: [],
      action: "addExam",
      fetchAPI: "",
      uploadAPI: "/uploadUnitExam",
      units: [],
      /////////////////////
      examId: null,
      examExist: false,
      ////////////////////
      loading: false,
      removing: false,
      textToQuestinos: "",
    };
  },
  computed: {
    examName() {
      return `${this.exam.stage}-${this.exam.unit}-${this.exam.year}-${this.exam.number}`;
    },
    questionsExist() {
      return !!localStorage.getItem("questions");
    },
    lessons() {
      return this.$store.getters.lessons.filter((lesson) => {
        return lesson.stage == this.exam.stage && lesson.section == this.exam.unit;
      });
    },
  },
  watch: {
    "exam.sections"(val) {
      console.log(val);
    },
    "exam.stage"() {
      this.getUnits();
    },
  },
  methods: {
    concatArraysOfQuestionsHandler(section, i) {
      this.collectedSections[i] = section;
    },
    async addExam() {
      eventBus.$emit("collectQuestions");
      await delay(200);
      console.log(this.collectedSections);
      if (!this.validate()) return;
      this.setExamFullDegree();
      this.loading = true;
      if (this.exam.dependentLesson == "") delete this.exam["dependentLesson"];
      axios
        .post(this.uploadAPI, { ...this.exam, sections: this.collectedSections })
        .then((res) => {
          this.loading = false;
          if (res.data.err) {
            this.examExist = false;
            return this.$store.dispatch("writemessage", res.data.err);
          }
          this.examExist = true;
          if (res.data.id) {
            this.exam._id = res.data.id;
            this.examId = res.data.id;
          }
          this.$store.dispatch("writemessage", "   ");
        });
    },
    /////////////////////////////////////////////////////////////////////
    /////////////////////////////////////////////////////////////////////
    increaseOneSection() {
      const section = {
        number: 0,
        titleType: "text",
        questionsType: 1,
        text: "  ",
        mimetype: "",
        questions: [
          {
            question: " ",
            contentType: "text",
            number: 0,
            fullDegree: 1,
            mimetype: "",
            choices: ["", "", "", ""],
            modalAnswer: "",
            typicalAnswer: "",
          },
        ],
      };
      this.exam.sections.push(section);
    },
    /////////////////////////////////////////////////////////////////////
    /////////////////////////////////////////////////////////////////////
    // gotoSolutionModel() {
    //   if (this.examExist) {
    //     this.$router.push("/dashboard/unit-exam-solutionModel/" + this.examId);
    //   }
    // },
    /////////////////////////////////////////////////////////////////////
    /////////////////////////////////////////////////////////////////////
    getUnits() {
      this.units = this.$store.getters.sections.filter(
        (unit) => unit.stage == this.exam.stage
      );
    },
    /////////////////////////////////////////////////////////////////////
    /////////////////////////////////////////////////////////////////////
    validate() {
      const errors = [];
      if (!this.exam.isTask && this.exam.name == "") errors.push("   ");
      // if (this.exam.number == "") errors.push("   ");
      if (this.exam.timer == 0) errors.push("   ");
      if (!this.exam.isTask && this.action == "addExam" && this.exam.number == "")
        errors.push("   ");
      if (
        !this.exam.isTask &&
        new Date(this.exam.deadLine).getTime() < new Date().getTime()
      )
        errors.push("     ");
      ///////////////////////////////////////////////////////////////
      this.collectedSections.forEach((sec) => {
        if (sec.text == "") errors.push(`   ${sec.number + 1}  `);
        sec.questions.forEach((ques, i) => {
          if (ques.question == "") {
            errors.push(`  ${i + 1}    ${sec.number + 1}  `);
          }
          if (ques.fullDegree == "") {
            errors.push(
              `   ${i + 1}    ${sec.number + 1}  `
            );
          }
          if (ques.choices.length < 2 && sec.questionsType == 1) {
            errors.push(
              `     ${i + 1}    ${
                sec.number + 1
              }   `
            );
          }
          if (!ques.modelAnswer || (ques.modelAnswer == "" && this.exam.autoCorrect)) {
            errors.push(
              `   ${i + 1}    ${sec.number + 1}  `
            );
          }

          ques.choices.forEach((opt, c) => {
            if (opt == "")
              errors.push(
                `  ${c + 1}    ${i + 1}    ${
                  sec.number + 1
                }  `
              );
          });
        });
      });
      if (errors.length > 0) {
        errors.forEach((error) => {
          alert(error);
        });
        return false;
      }
      console.log(errors);
      return true;
    },
    /////////////////////////////////////////////////////////////////////
    /////////////////////////////////////////////////////////////////////
    async copyQuestions() {
      eventBus.$emit("collectQuestions");
      await delay(400);
      await localStorage.setItem("questions", JSON.stringify(this.collectedSections));
      this.$store.dispatch("writemessage", "  ");
    },
    /////////////////////////////////////////////////////////////////////
    /////////////////////////////////////////////////////////////////////
    pasteQuestions() {
      const sections = JSON.parse(localStorage.getItem("questions"));
      if (sections) {
        this.exam = { ...this.exam, sections };
      }
    },
    /////////////////////////////////////////////////////////////////////
    /////////////////////////////////////////////////////////////////////
    download() {
      eventBus.$emit("collectQuestions");
      const json = JSON.stringify(this.collectedSections);
      const blob = new Blob([json], { type: "octet/stream" });
      const url = window.URL.createObjectURL(blob);
      // const url =
      //   "data:text/json;charset=utf-8," +
      //   encodeURIComponent(JSON.stringify(this.allQuestions));
      const a = document.createElement("a");
      a.download = this.exam.name + ".json";
      a.href = url;
      a.click();
      window.URL.revokeObjectURL(url);
    },
    /////////////////////////////////////////////////////////////////////
    /////////////////////////////////////////////////////////////////////
    getJson(e) {
      const file = e.target.files[0];
      if (file.type.split("/")[1] != "json") return alert(" json  ");
      const blob = new Blob([file], { type: "octet/stream" });
      const fileReader = new FileReader();
      fileReader.readAsText(blob);
      fileReader.addEventListener("loadend", (e) => {
        const sections = JSON.parse(e.srcElement.result);
        if (sections.length > 0) {
          console.log("Hello");
          console.log(sections);
          this.exam = { ...this.exam, sections };
        }
      });
    },
    /////////////////////////////////////////////////////////////////////
    /////////////////////////////////////////////////////////////////////
    async removeQuestions() {
      if (!confirm("  ")) return;
      this.exam.sections = [];
      localStorage.removeItem("questions");
      await delay(500);
      this.increaseOneSection();
    },
    /////////////////////////////////////////////////////////////////////
    /////////////////////////////////////////////////////////////////////
    removeSection(i) {
      this.exam.sections.splice(i, 1);
    },
    /////////////////////////////////////////////////////////////////////
    /////////////////////////////////////////////////////////////////////
    deleteExam() {
      const confirmed = confirm("     ");
      if (confirmed) {
        axios.delete("/deleteUnitExam/" + this.examId).then((res) => {
          if (res.data.err)
            return this.$store.dispatch("writemessage", "     ");
          this.$router.back();
        });
      }
    },
    /////////////////////////////////////////////////////////////////////
    /////////////////////////////////////////////////////////////////////
    deleteSolutions() {
      const confirmed = confirm("      ");
      if (confirmed) {
        this.loading = true;
        axios.delete("/deleteSolutions/" + this.examId).then((res) => {
          this.loading = false;
          if (res.data.err)
            return this.$store.dispatch("writemessage", "     ");
          return this.$store.dispatch("writemessage", "    ");
          this.$router.back();
        });
      }
    },
    /////////////////////////////////////////////////////////////////////
    /////////////////////////////////////////////////////////////////////
    setExamFullDegree() {
      this.exam.fullDegree = 0;
      for (let section of this.collectedSections) {
        for (let ques of section.questions) {
          this.exam.fullDegree += ques.fullDegree;
        }
      }
    },
    textToQuestinosHandler() {
      const res = convertTextToQuestions(this.textToQuestinos);
      if (res.error) return alert(error);
      this.exam.questions = res.questions;
    },
  },
};
</script>

