<template>
  <div :class="{ isTitle }">
    <div class="">
      <div class="d-flex justify-content-center">
        <v-radio-group v-model="contentType" row>
          <v-radio color="success" label="text" value="text">
            <template v-slot:label>
              <strong :style="{ color: isTitle ? '#fff' : '#000' }">Text</strong>
            </template>
          </v-radio>
          <!---->
          <v-radio color="success" label="text" value="html">
            <template v-slot:label>
              <strong :style="{ color: isTitle ? '#fff' : '#000' }">special</strong>
            </template>
          </v-radio>
          <!---->
          <v-radio color="success" label="text" value="image">
            <template v-slot:label>
              <strong :style="{ color: isTitle ? '#fff' : '#000' }">Image</strong>
            </template>
          </v-radio>
          <!----->
          <v-radio color="success" label="text" value="audio">
            <template v-slot:label>
              <strong :style="{ color: isTitle ? '#fff' : '#000' }">Audio</strong>
            </template>
          </v-radio>
          <v-radio color="success" label="text" value="video">
            <template v-slot:label>
              <strong :style="{ color: isTitle ? '#fff' : '#000' }">Video</strong>
            </template>
          </v-radio>
          <v-radio color="success" label="text" value="sketch">
            <template v-slot:label>
              <strong :style="{ color: isTitle ? '#fff' : '#000' }">sketch</strong>
            </template>
          </v-radio>
          <!-- <v-radio color="success" label="text" value="math">
            <template v-slot:label>
              <strong :style="{ color: isTitle ? '#fff' : '#000' }">Math</strong>
            </template>
          </v-radio> -->
        </v-radio-group>
      </div>
      <hr />
      <!---------------------------------------->
      <div v-if="contentType == 'text'">
        <textarea
          v-model="text"
          placeholder="The Question"
          class="form-control"
          rows="3"
        ></textarea>
      </div>
      <!---------------------------------->
      <!---------------------------------->
      <!---------------------------------->
      <div v-if="contentType == 'html'">
        <ckeditor v-model="text" :config="ckConfig" />
      </div>
      <!---------------------------------->
      <!---------------------------------->
      <!---------------------------------->
      <div v-if="contentType == 'image'" class="center">
        <div class="p-1">
          <input style="display: none" type="file" ref="pick" @change="pickFile" />
        </div>
        <div class="image" style="position: relative">
          <display-question v-if="text.includes('http')" :src="text" type="image" />
          <!----------------------------------------->
          <div v-if="!text.includes('http')" class="placeholder">
            <span v-if="loading">uploading...</span>
            <img
              @click="$refs.pick.click()"
              class="img-fluid"
              src="../../../assets/img/imageholder.webp"
            />
          </div>
        </div>
      </div>
      <!------------------------------------------------------------>
      <!------------------------------------------------------------>
      <!------------------------------------------------------------>
      <div v-if="contentType == 'audio'">
        <label for="formFileLg" class="form-label">upload mp3 file</label>
        <input
          class="form-control form-control-lg"
          id="formFileLg"
          type="file"
          @change="pickFile"
        />
        <!--------------------------------------------->
        <div class="audio center p-2" style="position: relative">
          <audio controls v-if="text.includes('audio-HH-')" :src="text" />
        </div>
      </div>

      <div v-if="contentType == 'video'">
        <label for="yt" class="form-label">Put youtube url</label>
        <input
          class="form-control form-control-lg"
          id="yt"
          type="text"
          :value="text"
          @change="setEmbedUrl"
        />
        <!--------------------------------------------->
        <div class="row justify-content-center p-2">
          <div class="col-md-6">
            <iframe
              width="100%"
              style
              controls
              v-if="text.includes('embed')"
              :src="text"
            />
          </div>
        </div>
      </div>
      <!--------------------------------------------->
      <!--------------------------------------------->
      <!--------------------------------------------->
      <div v-if="contentType == 'sketch'">
        <scetch :content="text" @saveSketch="saveSketch" />
      </div>
      <!--------------------------------------------->
      <!--------------------------------------------->
      <!--------------------------------------------->

      <!-- <div
        v-if="contentType == 'math'"
        style="
          background: #d3e2ddc4;
          border-radius: 10px;
          padding: 12px;
          font-size: 1.5rem;
        "
      >
        <math-input :config="config" v-model="text" />
        <hr />
        <div class="row justify-content-center">
          <div class="col-md-5">
            <v-btn class="ma-2" color="blue-grey" @click="saveMath"> save </v-btn>
          </div>
        </div>
      </div> -->
      <!--------------------------------------------->
      <!--------------------------------------------->
      <!--------------------------------------------->
      <div class="p-2 mt-3">
        <span v-if="loading">uploading...</span>
        <!----------------------------------------->
        <div v-if="progress > 0" class="progress">
          <div class="progressbar" :style="{ width: progress + '%' }">
            {{ progress }}%
          </div>
        </div>
        <div class="row justify-content-center">
          <div class="col-md-4">
            <button @click="removeFile" class="btn btn-outline-danger">Clear</button>
          </div>
        </div>
      </div>
    </div>
    <hr />
  </div>
</template>

<script>
// import { debounce } from "vue-debounce";
import { eventBus } from "../../../main";
import Scetch from "./sketch";
// import MathInput from "./mathlive";
import { delay, setEmbedUrl } from "../../../functions";
export default {
  props: ["examName", "sectionNumber", "questionNumber", "question", "align", "type"],
  //events: [
  //getQuestionText (text, contentType) to question.vue & questionSection.vue
  // ]
  components: {
    Scetch,
    // MathInput,
  },
  created() {
    this.text = this.question || "";
    this.contentType = this.type || "text";
  },
  mounted() {
    setInterval(() => {
      this.$emit("getQuestionText", this.text, this.contentType);
    }, 700);
    eventBus.$on("collectQuestions", () => {
      this.$emit("getQuestionText", this.text, this.contentType);
    });
    /////////////////// MATH INPUT ///////////////////
    // MathLive.renderMathInDocument();
  },
  data() {
    return {
      loading: false,
      text: "",
      contentType: "text",
      progress: 0,
      file: null,
      debounceTimer: null,

      //////MATH INPUT//////
      formula: "F(x)=",
      config: {
        smartMode: true,
        virtualKeyboardMode: "manual",
        readonly: true,
      },
      ckConfig: {},
    };
  },
  //////////////////////////////////////////////////////////////
  watch: {
    text(val) {
      const formats = [".jpg", "jpeg", "png", "webp"];
      // clearTimeout(this.debounceTimer);
      // this.debounceTimer = setTimeout(() => {
      //   this.$emit("getQuestionText", val, this.contentType);
      // }, 600);
      // else if (/<\/?[a-z][\s\S]*>/i.test(val)) this.contentType = "html";
      // else if (val.includes("{")) this.contentType = "math";
      if (val.startsWith("http") && val.includes("embed")) this.contentType = "video";
      else if (/<[a-z][\s\S]*>/i.test(val)) this.contentType = "html";
      else if (
        (val.startsWith("http") || val.includes("audio-HH-")) &&
        val.includes(".mp3")
      ) {
        this.contentType = "audio";
      } else if (val.startsWith("http") || val.includes("image-HH-"))
        this.contentType = "image";
      else this.contentType = "text";
    },
    question(val) {
      this.text = val || "";
    },
    type(val) {
      this.contentType = val || "text";
    },
    contentType(newVal) {
      const text = this.text;
      if (newVal != "html") this.text = text.replace(/(<([^>]+)>)/gi, "");
    },
  },
  computed: {
    adminData() {
      return this.$store.getters.adminData;
    },
    public_id() {
      return `${this.adminData.siteName.replace(/[^a-zA-Z0-9]+/g, "-")}/${
        this.contentType
      }-HH-${this.examName}-${this.sectionNumber}-${
        this.questionNumber ?? ""
      }-${new Date().getTime()}`;
    },
    isTitle() {
      return this.examName.includes("title");
    },
  },
  /////////////////////////////////////////////////////////////
  methods: {
    removeFile() {
      this.text = "";
    },
    pickFile(e) {
      let allowed = ["jpg", "jpeg", "png", "svg", "webp"];
      if (this.contentType == "audio") allowed = ["mpeg"];
      const file = event.target.files[0];
      if (!allowed.includes(file.type.split("/")[1])) {
        alert("invalid file format");
        return;
      }
      if (file.size > 3000000) {
        alert("file is large try smaller one");
        return;
      }
      this.file = file;
      this.uploadFile();
    },
    ////////////////////////////////////////////////////////////
    ////////////////////////////////////////////////////////////
    ///////////////////////////////////////////////////////////
    async uploadFile() {
      await delay(400);
      this.progress = 0;
      this.loading = true;
      this.contentType = "image";
      var fd = new FormData();
      fd.append("upload_preset", "cipuaphl");
      fd.append("tags", this.public_id); // Optional - add tag for image admin in Cloudinary
      console.log(this.file);
      fd.append("file", this.file);
      var url = `https://api.cloudinary.com/v1_1/farghaly-cloud2/upload?public_id=${this.public_id}`;
      var xhr = new XMLHttpRequest();
      xhr.open("POST", url, true);
      xhr.setRequestHeader("X-Requested-With", "XMLHttpRequest");

      xhr.upload.addEventListener("progress", this.setProgress);

      xhr.onreadystatechange = (e) => {
        this.progress = 0;
        this.loading = false;
        if (xhr.onerror) xhr.onerror = (xhr, (e) => console.log(e));
        if (xhr.readyState == 4 && xhr.status == 200) {
          // File uploaded successfully
          var response = JSON.parse(xhr.responseText);
          var url = response.secure_url;
          this.text = url;
          // Create a thumbnail of the uploaded image, with 150px width
          //   var tokens = url.split("/");
          //   tokens.splice(-2, 0, "w_150,c_scale");
          //   this.text = tokens.join("/");
          //   this.alt = response.public_id;
        } else {
        }
      };
      xhr.send(fd);
    },
    ///////////////////////////////////////////////

    setProgress(e) {
      this.progress = Math.round((e.loaded * 100.0) / e.total);
    },
    setEmbedUrl(e) {
      this.text = setEmbedUrl(e.target.value);
    },
    saveSketch(e) {
      var byteString = atob(e.split(",")[1]);

      // separate out the mime component
      var mimeString = e.split(",")[0].split(":")[1].split(";")[0];

      // write the bytes of the string to an ArrayBuffer
      var ab = new ArrayBuffer(byteString.length);
      var ia = new Uint8Array(ab);
      for (var i = 0; i < byteString.length; i++) {
        ia[i] = byteString.charCodeAt(i);
      }
      this.file = new Blob([ab], { type: "image/webp" });
      // this.file = new File([blob], "sketch", { type: "image/webp" });
      this.uploadFile();
    },
    ///////////////////////////// MATH INPUT ////////////////////
    saveMath() {
      console.log(this.text);
    },
  },
};
</script>

<style lang="scss" scoped>
span {
  text-align: center;
}
.placeholder {
  position: relative;
  span {
    position: absolute;
    top: 49%;
    left: 40%;
  }
}
.isTitle {
  label {
    color: #fff;
  }
  i {
    color: #fff;
  }
  span {
    color: #fff;
  }
}
.audio {
  display: block;
  width: 100%;
  audio {
    max-width: 100%;
    max-height: 100%;
    margin: 2px auto;
    object-fit: contain;
  }
}
</style>
