<template>
  <div class="section my-2">
    <div v-if="questionSection" class="title" :class="alignClass">
      <h2 class="subtitle-light">Section ({{ index + 1 }})</h2>

      <label :class="alignClass"
        ><strong
          >               
              ...
        </strong></label
      >

      <upload-question
        :examName="'title-' + examName"
        :sectionNumber="index"
        :question="questionSection.text"
        :type="questionSection.titleType"
        :align="alignClass"
        @getQuestionText="
          (text, type) => {
            this.questionSection.text = text;
            this.questionSection.titleType = type;
          }
        "
      />
      <hr />
      <div v-if="!autoCorrect" class="row" :class="alignClass">
        <div class="col-md-6">
          <label :class="alignClass"><strong> </strong></label>
          <select class="form-control" v-model="questionSection.questionsType">
            <option :value="0"></option>
            <option :value="1"></option>
          </select>
        </div>
      </div>
    </div>
    <!------------------------------------------------>
    <!------------------------------------------------>
    <!------------------------------------------------>
    <div
      v-if="questionSection.questions.length > 0"
      calss="questions"
      style="position: relative"
    >
      <v-card>
        <!-- <v-tabs v-model="tab" background-color="primary" dark>
          <v-tab v-for="(question, i) in questionSection.questions" :key="i">
            {{ i + 1 }}
          </v-tab>
        </v-tabs> -->

        <!-- <v-tabs-items v-model="tab">
          <v-tab-item v-for="(question, i) in questionSection.questions" :key="i"> -->
        <v-card flat>
          <v-card-text v-for="(question, i) in questionSection.questions" :key="i">
            <question
              :question="question"
              :examName="'question-' + examName"
              :sectionNumber="index"
              :align="alignClass"
              :questionsType="questionSection.questionsType"
              :index="i"
              @removeQuestion="removeQuestion"
              @updateQuestion="updateQuestion"
              :autoCorrect="autoCorrect"
            >
            </question>
          </v-card-text>
        </v-card>
        <!-- </v-tab-item>
        </v-tabs-items> -->
      </v-card>
    </div>
    <!-------------------------------------------------------->
    <!-------------------------------------------------------->
    <!---------
    ---------------------------------------------->
    <div class="row justify-content-center">
      <div class="col-md-4">
        <div class="plus-minus d-flex justify-content-between">
          <button
            @click="increaseOneQuestion()"
            class="btn btn-outline-success btn-rounded m-1"
          >
            <i class="fa fa-plus"></i>
             
          </button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Question from "./question.vue";
import UploadQuestion from "./upload-question";
export default {
  props: ["examName", "section", "index", "lang", "autoCorrect"],
  // events: [
  // concatArraysOfQuestions (questionSection, index) to addExam.vue
  // ]
  components: {
    Question,
    UploadQuestion,
  },
  created() {
    this.updateSection();
    if (this.autoCorrect) this.questionSection.questionsType = 1;
  },
  watch: {
    section() {
      this.updateSection();
    },
    autoCorrect(val) {
      if (val) this.questionSection.questionsType = 1;
    },
    index(val) {
      this.questionSection.number = val;
    },
  },
  data() {
    return {
      questionSection: {
        number: 0,
        titleType: "text",
        questionsType: 1,
        text: "  ",
        mimetype: "",
        questions: [
          {
            question: "",
            contentType: "text",
            number: 0,
            fullDegree: 1,
            mimetype: "",
            modalAnswer: "",
            typicalAnswer: "",
            choices: ["", "", "", ""],
            userAnswer: "",
            degree: 0,
          },
        ],
      },
      collectedQuestions: [
        {
          question: "",
          contentType: "text",
          number: 0,
          fullDegree: 1,
          mimetype: "",
          modalAnswer: "",
          typicalAnswer: "",
          choices: ["", "", "", ""],
        },
      ],
      tab: null,
    };
  },
  computed: {
    alignClass() {
      return this.lang == "2" ? "ltr" : "rtl";
    },
  },
  methods: {
    updateSection() {
      this.questionSection = this.section;
      this.questionSection.number = this.index;
    },
    updateQuestion(ques, index) {
      this.collectedQuestions[index] = ques;
      this.emitUpdate();
    },
    emitUpdate() {
      this.$emit(
        "concatArraysOfQuestions",
        { ...this.questionSection, questions: this.collectedQuestions },
        this.index
      );
    },
    removeQuestion(index) {
      this.questionSection.questions.splice(index, 1);
    },
    increaseOneQuestion() {
      const newQuestions = {
        question: "",
        number: 0,
        contentType: "text",
        fullDegree: 1,
        mimetype: "",
        typicalAnswer: "",
      };
      if (this.questionSection.questionsType == 1) {
        if (this.lang == "2") newQuestions["choices"] = ["A", "B", "C", "D"];
        else newQuestions["choices"] = ["", "", "", ""];
      }
      this.questionSection.questions.push(newQuestions);
      // this.tab = this.questionSection.questions.length - 1;
    },
  },
};
</script>
