<template>
  <div>
    <div class="ques" :class="align" :dir="align" style="position: relative">
      <div class="_close-absolute">
        <i @click="$emit('removeQuestion', index)" class="fa fa-times"></i>
      </div>

      <div class="">
        <h2 class="subtitle2">Question ({{ index + 1 }})</h2>
        <label :class="align"><strong> (  )</strong></label>
        <upload-question
          :examName="examName"
          :sectionNumber="sectionNumber"
          :questionNumber="index"
          :question="ques.question"
          :align="align"
          :type="ques.contentType"
          @getQuestionText="getQuestionText"
        />
        <!------------------------------------------------>
        <!------------------------------------------------>
        <!------------------------------------------------>
        <div class="d-flex flex-wrap justify-content-center align-items-center">
          <div class="form-group m-1">
            <label><strong> </strong></label>
            <input
              class="form-control"
              v-model.number="ques.fullDegree"
              type="number"
              :placeholder="'full degree'"
            />
          </div>

          <div class="form-group m-1" v-if="questionsType == 1">
            <label><strong> </strong></label>
            <input
              class="form-control"
              @change="changeQuestionChoices"
              type="number"
              min="2"
              max="6"
              :value="ques.choices.length"
            />
          </div>
        </div>
        <div v-if="questionsType == 1" class="_inputs2">
          <h4 class="subtitle">Choices</h4>
          <div class="row justify-content-center">
            <div class="col-md-3 center" v-for="(ch, c) in ques.choices" :key="c">
              <input
                class="form-control"
                v-model="ques.choices[c]"
                type="text"
                :placeholder="'Choice' + (c + 1)"
              />
            </div>
          </div>
          <!------------------------------------------------>
          <!------------------------------------------------>
          <div v-if="autoCorrect" class="center">
            <label> </label>
            <v-radio-group v-model="ques.modelAnswer" row>
              <v-radio
                v-for="(choice, i) in ques.choices"
                :key="i"
                color="success"
                :value="choice"
              >
                <template v-slot:label>
                  <strong>{{ choice }}</strong>
                </template>
              </v-radio>
            </v-radio-group>
          </div>
        </div>
        <!------------------------------------------------>
        <!------------------------------------------------>
        <!------------------------------------------------>
        <hr />
        <div class="form-group">
          <label :class="align"> </label>
          <textarea
            cols="30"
            rows="3"
            placeholder=" "
            class="form-control"
            v-model="ques['typicalAnswer']"
          ></textarea>
        </div>
      </div>
      <hr />
    </div>
  </div>
</template>

<script>
import UploadQuestion from "./upload-question";
export default {
  components: {
    UploadQuestion,
  },
  props: [
    "examName",
    "sectionNumber",
    "index",
    "question",
    "align",
    "questionsType",
    "autoCorrect",
  ],
  //events: updateQuestion (ques, index) to questionSection.vue

  ////////////////////////////////////////////////////////////
  ////////////////////////////////////////////////////////////
  created() {
    this.ques = this.question;
    this.ques.number = this.index;
  },
  ////////////////////////////////////////////////////////////
  ////////////////////////////////////////////////////////////
  watch: {
    questionsType(val) {
      if (val == 0) this.ques.choices = [];
      else if (val == 1) this.ques.choices = ["", "", "", ""];
    },
    question(val) {
      this.ques = val;
    },
    index(val) {
      this.ques.number = val;
    },
  },
  ////////////////////////////////////////////////////////////
  ////////////////////////////////////////////////////////////
  data() {
    return {
      ques: {
        question: "",
        contentType: "text",
        number: 0,
        fullDegree: 1,
        mimetype: "",
        modelAnswer: "",
        typicalAnswer: "",
        choices: ["", "", "", ""],
      },
    };
  },
  ////////////////////////////////////////////////////////////
  ////////////////////////////////////////////////////////////
  ////////////////////////////////////////////////////////////
  methods: {
    changeQuestionChoices(e) {
      const noc = e.target.value;
      const choices = [...this.ques.choices];
      const newChoices = [];
      for (let c = 0; c < noc; c++) {
        newChoices.push(choices[c] || "");
      }
      this.ques.choices = newChoices;
    },
    getQuestionText(text, type) {
      this.ques.question = text;
      this.ques.contentType = type;
      this.$emit("updateQuestion", this.ques, this.index);
    },
  },
};
</script>

<style lang="scss" scoped>
.questions {
  .question {
    // text-align: right;
    // direction: rtl;
    margin: 18px;
    padding: 15px;
    @media (max-width: 600px) {
      padding: 3px;
      margin: 3px;
    }
    // background-color: rgb(240, 241, 241);
    border: #555 1px solid;
    i {
      font-size: 1.8rem;
      margin: 3px;
    }
  }
}
</style>
