<template>
  <div class="">
    <div class="row justify-content-center align-items-stretch p-2 bg-dark m-4" dir="rtl">
      <div class="col-md-1">
        <input
          type="color"
          v-model="color"
          class="form-control p-1"
          style="height: 100%"
        />
      </div>
      <div class="col-md-1">
        <div
          @click="erease"
          style="cursor: pointer; background: #fff; height: 100%; border-radius: 6px"
        ></div>
      </div>
      <div class="col-md-1">
        <input
          v-model="lineWidth"
          type="range"
          min="1"
          max="25"
          value="5"
          class="form-control"
          style="height: 100%"
        />
      </div>
      <div class="col-md-2">
        <button @click="save" class="btn btn-success" style="height: 100%">save</button>
      </div>
    </div>
    <div class="center">
      <canvas
        id="canvas"
        v-on:mousedown="handleMouseDown"
        v-on:mouseup="handleMouseUp"
        v-on:mousemove="handleMouseMove"
        width="1000"
        height="400"
      ></canvas>
    </div>
  </div>
</template>
<script>
export default {
  mounted() {
    var c = document.getElementById("canvas");
    this.ctx = c.getContext("2d");
    this.ctx.imageSmoothingEnabled = true;
    this.canvas = document.getElementById("canvas");
    // canvas.height = canvas.width * 1.1;
  },
  data: () => {
    return {
      canvas: null,
      ctx: null,
      color: "#000",
      lineWidth: 2,
      mouse: {
        current: {
          x: 0,
          y: 0,
        },
        previous: {
          x: 0,
          y: 0,
        },
        down: false,
      },
    };
  },
  computed: {
    currentMouse: function () {
      var c = document.getElementById("canvas");
      var rect = c.getBoundingClientRect();
      // var rect = {left: 0, top: 0}
      return {
        x: this.mouse.current.x - rect.left,
        y: this.mouse.current.y - rect.top,
        // x: (this.mouse.current.x - rect.left) / (rect.right - rect.left) * c.width,
        // y: (this.mouse.current.y - rect.top) / (rect.bottom - rect.top) * c.height
      };
    },
  },
  watch: {},
  methods: {
    draw: function (event) {
      if (this.mouse.down) {
        this.ctx.strokeStyle = this.color;
        if (event.color) this.ctx.strokeStyle = event.color;
        this.ctx.lineWidth = this.lineWidth;
        if (event.color) this.ctx.lineWidth = event.lineWidth;
        this.ctx.lineTo(this.currentMouse.x, this.currentMouse.y);
        this.ctx.stroke();
      }
    },

    handleMouseDown: function (event) {
      const rect = document.getElementById("canvas").getBoundingClientRect();
      console.log(rect);
      //   window.scrollTo(0, 1250);
      this.mouse.down = true;
      this.ctx.closePath();
      this.ctx.beginPath();

      this.mouse.current = {
        x: event.clientX,
        y: event.clientY,
      };
      this.ctx.moveTo(this.currentMouse.x, this.currentMouse.y);
    },
    handleMouseUp: function () {
      this.mouse.down = false;
    },

    handleMouseMove: function (event) {
      if (!this.mouse.down) return;
      this.mouse.current = {
        x: event.clientX,
        y: event.clientY,
      };
      this.draw(event);
    },
    erease() {
      this.ctx.clearRect(0, 0, 2000, 2000);
    },
    save() {
      const data = this.canvas.toDataURL("iamge/webp", 0.7);
      this.$emit("saveSketch", data);
    },
  },
};
</script>
<style scoped lang="scss">
canvas {
  //   width: 100%;
  background: white;
  // height: 100vh;
  box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.2);
}
</style>
